import { async } from 'q';
import React, { Component } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner';
import PropTypes from 'prop-types'

export class News extends Component {
    static defaultProps = {
        country:"in",
        category:"general",
        pageSize:5
    }
    static propTypes ={
        country: PropTypes.string,
        category:PropTypes.string,
        pageSize:PropTypes.number
    }
    constructor() {
        super();
        this.state = {
            articles: [],
            loading: false,
            page: 1,
            pageSize: 20
        }
    }

    async componentDidMount() {
        let URL = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=5aa366202e4d4c5eacdc82bbb91a67fa&page=1&pageSize=${this.props.pageSize}`
        this.setState({loading:true})
        let data = await fetch(URL);
        let parsedData = await data.json();
        this.setState({ articles: parsedData.articles, totalResults: parsedData.totalResults,loading:false ,publishedAt:parsedData.publishedAt,author:parsedData.author })
    }
    handlePrevClick = async () => {

        let URL = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=5aa366202e4d4c5eacdc82bbb91a67fa&page=${this.state.page - 1}&pageSize=${this.props.pageSize}`
        this.setState({loading:true})
        let data = await fetch(URL);
        let parsedData = await data.json();
        this.setState({
            page: this.state.page - 1,
            articles: parsedData.articles,
            loading:false
        })
    }

    handleNextClick = async () => {
        if (!(this.state.page + 1 > Math.ceil(this.state.totalResults /  this.props.pageSize))) {
            let URL = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=5aa366202e4d4c5eacdc82bbb91a67fa&page=${this.state.page + 1}&pageSize=${this.props.pageSize}`;
            this.setState({loading:true})
            let data = await fetch(URL);
            let parsedData = await data.json();
            this.setState({
                page: this.state.page + 1,
                articles: parsedData.articles,
                loading:false
            })
            console.log("Hello world");
        }

    }

    render() {
        return (
            <div className='container my-3'>
                <h2>S21k News - Top Headlines</h2>
                {this.state.loading && <Spinner/>}
                <div className="row">
                    {!this.state.loading && this.state.articles.map((element) => {
                        return <div key={element.url} className="col-md-4 my-3">
                            <NewsItem newsTitle={element.title} newsDiscription={element.description} newsImageURL={element.urlToImage} newsURL={element.url} publishedAt={element.publishedAt} author={element.author} />
                        </div>
                    })}
                </div>
                <div className="container d-flex justify-content-between">
                    <button disabled={this.state.page <= 1} type="button" className="btn btn-dark" onClick={this.handlePrevClick}>&larr; Previous</button>
                    <button disabled={this.state.page + 1 > Math.ceil(this.state.totalResults /  this.props.pageSize)} type="button" className="btn btn-dark" onClick={this.handleNextClick}>Next &rarr;</button>
                </div>
            </div>
        )
    }
}

export default News
